import React, { Component } from 'react'
import Thumbnail from './Thumbnail';
import GetSheetDone from 'get-sheet-done';
const DOCUMENT_ID = '15fwGrqpGgbVg2P2JIOOIuUz9w5p0Pnq1a4FNY9roPgw'
export default class Experiences extends Component {
    constructor(props){
        super(props)
        this.state = {Adam:[], opened:localStorage.getItem("tidbits-opened")}
        this.closeAllBut = this.closeAllBut.bind(this)
    }
    closeAllBut(number){
        this.setState({opened:number},localStorage.setItem("tidbits-opened",number))
    }
    componentDidMount(){
        GetSheetDone.labeledCols(DOCUMENT_ID).then(
          sheet =>{
            let data = sheet.data;
            console.log(data)
            this.setState({
              Adam:data
            })
          })
      }
  render() {
    return (
      <div>
        {this.state.Adam.map(v => <Thumbnail opened={this.state.opened===v.onewordname} closeAllBut={this.closeAllBut} num={v.onewordname} origin={v}/>)}
      </div>
    )
  }
}



