import React, { Component } from 'react'
import styled from 'styled-components';
import AnimateHeight from 'react-animate-height';
export default class Thumbnail extends Component {
    componentDidMount(){
        if(localStorage.getItem(`tidbits-isComp-${this.props.num}`)){
            this.setState({completed:true})
        }
        if(localStorage.getItem(`tidbits-Exp-${this.props.num}`)){
            this.setState({exp:localStorage.getItem(`tidbits-Exp-${this.props.num}`)})
        }
    }
    constructor(props) {
        super(props)
        this.state = { completed: false,exp:""}
        this.handleClick = this.handleClick.bind(this)
        this.handleComplete = this.handleComplete.bind(this)
        this.handleTextAreaWriting = this.handleTextAreaWriting.bind(this)
    }
    handleTextAreaWriting(event){
        this.setState({exp:event.target.value},localStorage.setItem(`tidbits-Exp-${this.props.num}`,event.target.value))
    }
    handleClick() {
        if (this.props.opened) {
            this.props.closeAllBut(null)
        }
        else {
            this.props.closeAllBut(this.props.num)
        }
    }
    handleComplete(){
        if(!this.state.completed){
            this.setState({completed:true},localStorage.setItem(`tidbits-isComp-${this.props.num}`,"true"));
        }
        else{
            var response = window.confirm("Are you sure you want to revoke the completion? Your data will be lost.");
            if(response){
                this.setState({completed:false},
                    () => {
                    localStorage.removeItem(`tidbits-isComp-${this.props.num}`)
                    localStorage.removeItem(`tidbits-Exp-${this.props.num}`)
                    }
                )
            }
        }
    }
    render() {
        const {paragraph1,paragraph2,paragraph3}=this.props.origin;
        return (
            <Wrapper>
                <Title className="title" onClick={this.handleClick}>{this.state.completed && <Success></Success>}{this.props.origin.onelinename}</Title>
                <Time><Clock></Clock> {this.props.origin.time} minutes</Time>
                <AnimateHeight
          duration={ 500 }
          height={ this.props.opened ? "auto":"0" } // see props documentation bellow
        >
            <ExperimentText>
                    {paragraph1 && <p>{paragraph1}</p>}
                    {paragraph2 && <p>{paragraph2}</p>}
                    {paragraph3 && <p>{paragraph3}</p>}
                    </ExperimentText>
                    <ExperimentButton>
                        <Button onClick={this.handleComplete} className={this.state.completed?"completed":""}> {this.state.completed?"Completed":"Mark As Completed"}</Button>
                    </ExperimentButton>
                    {this.state.completed && <TextArea value={this.state.exp} onChange={this.handleTextAreaWriting} placeholder="How was the experience?"/>}
                    </AnimateHeight>
            </Wrapper>
        )
    }
}
const Success= styled.span`
color:green;
padding:0px 12px;
font-size:1.1em;
`
const TextArea = styled.textarea`
width:90%;
margin-top:25px;
height:150px;
`
const Button = styled.span`
padding:10px 20px;
background:#e3e3e3;
color:#444;
font-size:0.8em;
margin-top:2em;
transition:0.3s ease-in all;
&:hover{
    cursor:pointer;
    background:yellow;
    color:black;
}


`
const ExperimentButton = styled.div`
.completed{
    background:lightgreen;
}
`
const ExperimentText = styled.div`
padding:20px 0;
`
const Wrapper = styled.div`
.opened{
    display:block;
    -webkit-animation: fadeIn 1s;
    animation: fadeIn 1s;
}
@-webkit-keyframes fadeIn {
    from { opacity: 0; max-height:0}
      to { opacity: 1; max-height:500px;display:block}
}  
@keyframes fadeIn {
    from { opacity: 0; max-height:0;}
      to { opacity: 1; max-height:500px; display:block}
}
.closed{
    display:none;
}
padding:2vw 5vw;
border-bottom: 1px solid #f0f0f0;
@media only screen and (max-width: 600px) {
    padding:5vw 2vw;
  }
&:hover{
    div.title{
        color:turquoise;
        transition: 0.2s ease-in all;
    }
}
`
const Title = styled.div`
font-weight:700;
font-size:1.2em;
color: #54514f;
transition: 0.2s ease-in all;
cursor:pointer;

`
const Time = styled.div`
color:#cdcdcd;
font-size:0.8em;
`
const Clock = styled.span`
font-size:1.5em;
`


